.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  background-color: white;
}
.login__logo {
  width: 150px;
  object-fit: contain;
  margin-top: 20px;
  margin-bottom: 20px;
}
.login__container {
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: solid 1px lightgray;
}
.login__container > h1 {
  font-weight: 500;
  font-size: 20px;
}
.login__container > form > h5 {
  margin-bottom: 5px;
}
.login__container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: white;
  width: 98%;
}
.login__container > p {
  font-size: 12px;
  margin-top: 15px;
}
.login__signInButton {
  background-color: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  margin-top: 10px;
  border: solid 1px;
  border-color: #a66734 #9c7e31 #846a29;
  color: #111;
}
.login__registerButton {
  border-radius: 2px;
  width: 100%;
  height: 30px;
  margin-top: 10px;
  border: solid 1px;
  border-color: darkgray;
}
